<template>
    <div class="app">
        <div class="currencyName ov">
            <label class="fl" for="name">请选择币种：</label>
            <input class="fl currencyN" readonly v-model="currency.name" type="text" id="name" placeholder="请输入币种名称">
            <router-link class="fr" to="/currency?redirect=redcreate">切换币种</router-link>
        </div>
        <div class="currencyName ov">
            <label class="fl" for="group">群名称：</label>
            <input class="fl" v-model="createForm.name" type="text" id="group" placeholder="请输入群名称">
        </div>
        <div class="currencyName ov">
            <label class="fl" for="money">红包总金额：</label>
            <input class="fl" v-model="redmoney" type="text" id="money" placeholder="请输入总金额">
        </div>
        <div class="currencyName ov">
            <label class="fl" for="moneyNum">红包个数：</label>
            <input class="fl" v-model="createForm.count" type="text" id="moneyNum" placeholder="请输入红包个数">
        </div>
        <div class="currencyName ov">
            <label class="fl" for="banlce">账户余额：</label>
            <input class="fl" type="text" id="banlce" v-model="balance" readonly>
        </div>
        <div class="getRedpack">
            <div class="getredTag">下一个发红包：</div>
            <div class="minGet">
                <input v-model="createForm.send_rule" value="1" type="radio" id="minlab" name="getRedpack" />
                <label for="minlab">抢到最少的发</label>
            </div>
            <div class="maxGet">
                <input v-model="createForm.send_rule" value="2" type="radio" id="maxlab" name="getRedpack" />
                <label for="maxlab">抢到最多的发</label>
            </div>
        </div>
        <div class="setPassword getRedpack">
            <div class="getredTag">设置房间密码：</div>
            <div class="minGet">
                <input v-model="createForm.need_pass" value="1" type="radio" id="yeslab" name="setPassword" />
                <label for="yeslab">是</label>
            </div>
            <div class="maxGet">
                <input v-model="createForm.need_pass" value="0" type="radio" id="nolab" name="setPassword" />
                <label for="nolab">否</label>
            </div>
        </div>
        <input v-if="createForm.need_pass == 1"  v-model="createForm.password" type="password"  placeholder="请输入您的房间密码" />
        <div class="art">
            <p>有密码的房间不会被推荐到首页，需要您自己一一邀请朋友加入</p>
        </div>
        <div class="art">
            <div>创群首发红包消耗：{{createMoney}}{{currency.name}}</div>
            <p>1、其中一半的{{currency.name}}作为群主首发红包的消耗，另一半的{{currency.name}}作为群主押金，作为后面更新版本后的玩法押金。</p>
            <p>2、创群成功后，您的收益为群内每个红包总额的3%。</p>
        </div>
        <div class="sBtn"><button @click="createRed" :disabled="createdisabled">创建</button></div>
    </div>

</template>

<script>
    import treeBar from '@/page/tree/treeBar'
    import { createRed } from '@/api/red'
    import { mycurrency } from '@/api/user'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import Dialog from 'vant/lib/dialog';
    import 'vant/lib/dialog/style';
    import fmath from '@/utils/math'
    import { isNumber } from "vant/es/utils/validate/number";
    import { currencyFirst, currencyInfo} from "@/api/index";
    export default {
        name: "Redcreate",
        components: {
            treeBar
        },
        data()
        {
            return {
                currency_id:'',
                balance:0,
                currency:{
                  name:'',
                  id:''
                },
                redmoney:'',
                createMoney:0,
                createForm:{

                    currency_id:'',
                    money:0,
                    name:'',
                    count:'',
                    send_rule:1,
                    need_pass:0,
                    password:''

                },
                createdisabled:false
            }
        },
        watch:{

            redmoney(val)
            {
                if (isNumber(val) && val > 0)
                {
                    this.createMoney = fmath.floatMul(val, 2)
                } else
                {
                    this.createMoney = 0
                }

                this.createForm.money = val
            }
        },
        created()
        {
            this.pageInit()
        },
        mounted()
        {

        },
        methods:{

            async pageInit()
            {

                this.currency_id = this.$route.query.currencyId
                /** 处理一下 默认先为1 */
                this.currency_id  =  this.currency_id ?  this.currency_id : 1;
                if (this.currency_id)
                {
                    this.createForm.currency_id = this.currency_id
                    let res = await currencyInfo(this.currency_id)
                    this.currency = res
                    this.getBalance()
                }


            },
            // 获取当前币种余额
            getBalance()
            {
                mycurrency({currency_id:this.currency_id}).then(res => {
                    this.balance = res.num
                })
            },
            // 创建红包
            createRed()
            {
                if (this.createForm.name == '' || this.createForm.name.length == 0)
                {
                    Toast('请输入群名称')
                    return
                }
                if (this.createForm.money == '' || this.createForm.money.length == 0)
                {
                    Toast('请输入红包金额')
                    return
                }
                if (!isNumber(this.createForm.money) || this.createForm.money <= 0)
                {
                    Toast('请输入合法红包金额')
                    return
                }
                if (!isNumber(this.createForm.count) || this.createForm.count < 2)
                {
                    Toast('红包个数不能小于2')
                    return
                }

                if (this.createForm.need_pass == 1)
                {
                    if (!this.createForm.password)
                    {
                        Toast('请输入房间密码')
                        return
                    }
                    if (this.createForm.password.length < 6)
                    {
                        Toast('请输入房间密码不能少于6位数')
                        return
                    }

                }
                Toast.loading({
                    duration: 0,       // 持续展示 toast
                    forbidClick: true, // 禁用背景点击
                    loadingType: 'spinner',
                });
                this.createdisabled = true
                createRed(this.createForm).then(res => {
                    Toast.clear()
                    Dialog.alert({
                        title: '创建红包',
                        message: res.message ? res.message :'创建成功，快去邀请抢红包吧！',
                        confirmButtonText:'马上去'
                    }).then(() => {

                        this.$router.push('/redchat?groupId='+res.redgroup_id)
                    });
                })
            }




        }
    }
</script>

<style scoped>
    .app .currencyName {
        margin: 0.2rem 0 0 0.15rem;
        border-bottom: 0.01rem solid #F5F5F5;
        padding: 0 0.15rem 0.2rem 0;
    }
    .app .currencyName label {
        display: block;
        font-size: 0.16rem;
        color: #222222;
    }
    .app .currencyName input {
        font-size: 0.15rem;
        color: #ADADAD;
        width: 60%;
        padding-left: .04rem;
    }
    .app .currencyName .currencyN{
        display: inline-block;
        width: 30%;
    }
    .app .currencyName a {
        width: 0.8rem;
        height: 0.2rem;
        line-height: 0.2rem;
        display: inline-block;
        float: right;
        background: #E33C3D;
        text-align: center;
        font-size: 0.13rem;
        color: #fff;
        border-radius: 0.02rem;
    }
    .app .getRedpack {
        width: 3.75rem;
        height: 0.58rem;
        line-height: 0.58rem;
        background-color: #fff;
        border-top: 0.1rem solid #F5F5F5;
        display: flex;
        border-bottom: 0.01rem solid #f5f5f5;
    }
    .app .getRedpack .getredTag {
        font-size: 0.16rem;
        color: #222222;
        box-sizing: border-box;
        margin-left: 0.15rem;
    }
    .app .getRedpack .minGet {
        font-size: 0.12rem;
        color: #999999;
        box-sizing: border-box;
        margin-right: 0.2rem;
    }
    .app .getRedpack .maxGet {
        font-size: 0.12rem;
        color: #999999;
        box-sizing: border-box;
    }
    .app .getRedpack input[type="radio"] + label::before {
        content: "\a0";
        /*不换行空格*/
        display: inline-block;
        vertical-align: middle;
        font-size: 18px;
        width: 0.12rem;
        height: 0.12rem;
        margin-right: 0.1rem;
        border-radius: 50%;
        border: 1px solid #b8b8be;
        text-indent: 0.15rem;
        line-height: 1;
        transform: translate(0rem, -0.02rem);
    }
    .app .getRedpack input[type="radio"]:checked + label::before {
        background-color: #e33c3d;
        border: 1px solid #e33c3d;
        width: 0.1rem;
        height: 0.1rem;
        background-clip: content-box;
        padding: 1px;
    }
    .app .getRedpack input[type="radio"]:checked + label {
        color: #e33c3d;
    }
    .app .getRedpack input[type="radio"] {
        position: absolute;
        clip: rect(0, 0, 0, 0);
    }
    .app .setPassword {
        margin-top: 0;
        border-top: 0;
    }
    .app input[type="password"] {
        width: 3.75rem;
        padding-left: 0.15rem;
        box-sizing: border-box;
        background-color: #fff;
        font-size: 0.15rem;
        color: #adadad;
        transform: translateY(-0.15rem);
    }
    .app .art {
        border-top: 0.01rem solid #F5F5F5;
        padding: 0.15rem;
    }
    .app .art div {
        font-size: 0.16rem;
        color: #222222;
        margin-bottom: 0.1rem;
    }
    .app .art p {
        font-size: 0.12rem;
        color: #999999;
    }
    .app .sBtn {
        text-align: center;
        margin-top: 0.5rem;
    }
    .app .sBtn button {
        width: 3.45rem;
        color: #F5F5F5;
        font-size: 0.17rem;
        background: #E33C3D;
        border-radius: 0.02rem;
        height: 0.45rem;
    }
</style>



